  z-index: -1;
}*/

/* responsive code */

@media (max-width: 991px) {
  #header {
    height: 80vh;
    text-align: center;
  }

  #header .header-img {
    text-align: center;
  }
  #header .header-img img {
    width: 60%;
  }

  footer p {
  padding: 20px;
  margin: 20px;
  text-align: center;
  position: -webkit-sticky;
  position: sticky;
  }
}

@media (max-width: 768px) {
  #header {
    margin-top: 20px;
  }
  #header h1 {
    font-size: 28px;
  }
  #header h2 {
    font-size: 18px;
    margin-bottom: 30px;
  }
  #header .header-img img {
    width: 70%;
  }
  footer p {
  padding: 20px;
  margin: 20px;
  text-align: center;
  position: -webkit-sticky;
  position: sticky;
   }
}
